import React from 'react'
import ImageUpload from 'react-images-upload'
import AddArticle from './addArticle.js'
import ViewArticle from './viewArticle.js'
export default class Admin extends React.Component{
	constructor(props){
		super(props)
		this.state={
			add:false,
			view:false
		}
		this.addArticle= this.addArticle.bind(this)
		this.viewArticle = this.viewArticle.bind(this)
		
		this.logout = this.logout.bind(this)
	}
	logout(){

		this.props.logout(false)
	}
	addArticle(){
		this.setState({
			add:true,
			view:false
		})
	}
	viewArticle(){
		this.setState({
			add:false,
			view:true
		})
	}
	render(){
		return(
			<div className="admin">
				<div className="fixed-top pt-3 pl-2 mb-5 pb-3">
					<h2 className="ed-head d-inline">Edaptly</h2>
					<button className="mr-4 text-right d-inline btn btn-primary" onClick={this.logout}>LogOut</button>
				</div>
				<div className="admin-btn text-center mb-3">
					<div>
						<button className="btn btn-primary p-5 mr-3 mb-3" onClick={this.addArticle}>Add Article</button>
						<button className="btn btn-primary p-5 mb-3" onClick={this.viewArticle}>View Article</button>
					</div>
				</div>
				{this.state.add ? 
					<AddArticle/> : <span></span>
				}
				{this.state.view ? 
					<ViewArticle/> : <span></span>
				}	
			</div>
			)
	}
}