import React from 'react'
import Header from './header'
import Scroll from './scrollTheme.js'
import BlogCard from './BlogCard.js'
import data from '../moc-db.json'
import DatePicker from 'react-date-picker' 
import Slider from "react-slick"
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
import Modal from 'react-responsive-modal'
import CardExpanded from './CardExpanded.jsx'

export default class Current extends React.Component{
	constructor(props){
		super(props)
		this.state={
			loading:false,
			latestList:data,
			open:false,
			popUpValue:[],
			showCard:true,
			date: new Date()
		}
		this.openPopUp = this.openPopUp.bind(this)
		this.closeDialog= this.closeDialog.bind(this)
		this.toggle = this.toggle.bind(this)
		this.dateChange= this.dateChange.bind(this)
	}
	dateChange(value){
		console.log(value)
		this.setState({
			date:value
		})
	}
	toggle(){
		this.setState({
			showCard:!this.state.showCard
		})
	}
	openPopUp(value){
		console.log('inside home with value',value)
		this.props.open(true,value)
	}
	closeDialog(){
		this.setState({
			open:false,
			popUpValue:[]
		})
	}
	// <div className="mt-3 pl-1 row justify-content-center">
	// 				<div className="col-md-2 col-lg-2 col-sm-2 mt-4"><h2 className="head">Today's Current</h2> </div> 
	// 				<div className="col-sm-10 col-md-10 col-lg-10">	
	// 					<DatePicker  value={this.state.date} onChange={this.dateChange}/>
	// 					<span className="mt-3  tog" onClick={this.toggle}><i class="arrow1 down-arrow"></i></span>
	// 				</div>
	// 				</div>
	render(){
		const settings = {
      dots: false,
      arrows: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,

    };

		
		return(
			<div>
				
				<div className="today-current mt-4 mb-4 mr-2 ml-2 pb-5 pr-4 pl-4 ">
					
				{this.state.showCard ? 
					
						<Slider {...settings} > 
						
               		{this.state.latestList.map((e, i) =>
                      (
                      <BlogCard
                      showExpanded={this.openPopUp}
                        key={i}
                        title={e.title}
                        subtitle={e.subTitle}
                        cont={e.content}
                        views={e.views}
                        lang={e.lang}
                        author={e.author}
                        authorImg={e.authorImg}
                        thumbnail={e.thumbnail}
                        style={e.style}
                        metatags={e.metaTag}
                        tags={e.tag}
                        _id={e._id}
                      />
                    )
                  )
            }
            </Slider>
            
            :
            <span></span>
        }
		</div>
				 

			</div>
			)
	}
}