import React, { Component } from 'react'
import './Blogcard.scss'

export default class Card1 extends Component {
    constructor(props) {
        super(props);
        this.state = {
           showPopUp:false,
           props:[]
        }

        this.openPopUp= this.openPopUp.bind(this)
        this.createMarkup=this.createMarkup.bind(this)
        this.newString=''
    }
    openPopUp(value,gprops){
        this.setState({
            showPopUp:true,
            props:gprops
        })
        this.props.showExpanded(true,gprops)
    }
    createMarkup(){
        let n=this.props.cont
        if(this.props.cont.length>130){
            n=this.props.cont.substring(0,120)
        }
        return { __html: n}
    }
render(){
    return (<div className="blogCard latest card text-left " onClick={()=>this.openPopUp(true,this.props)}>
                <div className="lang">
                    {this.props.lang}
                </div>
                <div className="metatag orange-bar">
                    {this.props.metatags}
                </div>
                <img src={this.props.thumbnail  ? this.props.thumbnail : 'https://media.istockphoto.com/photos/abstract-background-picture-id537447438?k=6&m=537447438&s=612x612&w=0&h=YF98lsh0OIxChOs4XmOFmuzLxSj8sieD-CiK-V8iGfU='} className="card-img-top" alt="..." />
                <div className="card-body">
                    <h4 className="card-title col-bl">{this.props.title || "Title"}</h4>
                    <p className="col-bl mt-n2">{this.props.subtitle || "Subttitle"}</p>
                    <p className="card-text mt-n2">
                        
                    </p>
                    <div dangerouslySetInnerHTML={this.createMarkup()} ></div>
                </div>
                <div className="card-footer">
                    
                    {this.props.authorImg ? <img src={this.props.authorImg} className="authorImg" /> : ''}
                    by {this.props.author}
                </div>
            </div>)
}
}