import React from 'react'
import Card1 from './card1.js'
import Slider from "react-slick"
import Card2 from './card2.js'
export default class ShowCard extends React.Component{
	constructor(props){
		super(props)
		this.callback=this.callback.bind(this)
	}
	callback(value,gprops){
		this.props.open(true,gprops)
		
	}
	render(){const settings = {
      dots: false,
      arrows: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,

    };
		return(
				<div>
				<Slider {...settings} >
					{this.props.value.text.map((e,i)=>(
						<Card1 
						showExpanded={this.callback}
							key={i}
							title={e.title}
                        subtitle={e.subTitle}
                        cont={e.content}
                        views={e.views}
                        lang={e.lang}
                        author={e.author}
                        thumbnail={e.photo}
                        metatags={e.metaTag}
                        tags={e.tag}
                        _id={e._id}
						/>
					))}
					</Slider>
				</div>
			)
	}

}


		 