import React from 'react'
import superagent from 'superagent'
export default class Admin extends React.Component{
	constructor(props){
		super(props)
		this.state={
			btnText:'add'
		}
		this.inputEmail=React.createRef()
		this.inputPass = React.createRef()
		this.add = this.add.bind(this)
	}
	add(){
		superagent.post('/api/admin')
		.send({'email':this.inputEmail.current.value, 'password':this.inputPass.current.value})
		.then((res)=>{
			this.setState({
							btnText:res.text
						})

		})
	}
	render(){
		return(
			<div>
				<input className="form-group" ref={this.inputEmail} type="text"/>
				<input className="form-group" ref={this.inputPass} type="text"/>
				<button className="btn btn-primary" onClick={this.add}>{this.state.btnText}</button>
			</div>
			)
	}
}