import React from 'react'
import superagent from 'superagent'
import PanelComponent from '../components/admin'

export default class admin extends React.Component{
	constructor(props){
		super(props)
		this.state={
			btnText:'Submit',
			showNext:false
		}
		this.inputEmail=React.createRef()
		this.inputPassword= React.createRef()
		this.formSubmit = this.formSubmit.bind(this)
		this.callback= this.callback.bind(this)
	}
	callback(value)	{
		if(value===false){
			localStorage.removeItem('token')
			this.setState({
				showNext:false
			})
		}
	}
	componentDidMount(){
		if(localStorage.getItem('token')){
			superagent.get('http://localhost:8080/api/adminLogin')
			.set({'authorization':localStorage.token})
			.then((res)=>{
				if(res.text==='Verified')
					this.setState({
						showNext:true
					})
			})
		}
	}
	formSubmit(e){
		e.preventDefault()
		this.setState({
			btnText:'Checking...'
		})
		superagent.post('http://localhost:8080/api/adminLogin')
		.send({email: this.inputEmail.current.value ,password: this.inputPassword.current.value})
		.then((res)=>{
			console.log('res',res)
			res.text=JSON.parse(res.text)
			console.log('res text',res.text)
			if(res.text.message==="Success")	
			{
				localStorage.setItem('token',res.text.token)
				this.setState({
					btnText:'Success!',
					showNext:true
				})
			}
			else
				this.setState({
					btnText: 'Wrong Password.'
				})



		})
	}
	render(){
		return(
			<div className="admin-panel">
				{ !this.state.showNext ? 
				<div className="center-dialog">
					<h3 className="text-center black-header pt-3 pb-5">Login</h3>
					<form className="pl-3 pr-3" onSubmit={this.formSubmit}>
					  <div class="form-group">
					    <label for="exampleInputEmail1">Email address</label>
					    <input type="email" class="form-control" id="exampleInputEmail1" ref={this.inputEmail} aria-describedby="emailHelp" placeholder="Enter email"/>
					  </div>
					  <div class="form-group">
					    <label for="exampleInputPassword1">Password</label>
					    <input type="password" class="form-control" ref={this.inputPassword} id="exampleInputPassword1" placeholder="Password"/>
					  </div>
					   <button type="submit" class="btn btn-primary pb-2 mb-3 text-center">{this.state.btnText}</button>
					</form>
				</div>
				 :
				 <PanelComponent logout={this.callback}/>
				} 
			</div>
			)
	}
}