import React from 'react'


export default class categoryCards extends React.Component{
	constructor(props){
		super(props)
		this.class="card"+this.props.style
		this.class2="card"+this.props.sstyle
		this.clicked=this.clicked.bind(this)
	}
	clicked(value){
		console.log('value',value)
		this.props.click(value)
	}
	render(){
		return(
			<div>
			<div className="categoryCards"onClick={()=>this.clicked(this.props.text)}>
				<div className={this.class}>
					<p className="text align-bottom pl-1">{this.props.text}</p>
				</div>
			</div>
			<div className="categoryCards" onClick={()=>this.clicked(this.props.secondText)}>
				<div className={this.class2}>
					<p className="text align-bottom pl-1">{this.props.secondText}</p>
				</div>
			</div>
			</div>
			)
	}
}