import React, { Component } from 'react'
import './Blogcard.scss'

export default class Card2 extends Component {
    constructor(props) {
        super(props);
        this.state = {
           showPopUp:false,
           props:[]
        }
        this.openPopUp= this.openPopUp.bind(this)
        
    }
    openPopUp(value,gprops){
        this.setState({
            showPopUp:true,
            props:gprops
        })
        this.props.showExpanded(gprops)
    }

    render(){
        return(
             <div className="blogCard featured card text-left " onClick={()=>this.openPopUp(true,this.props)}>
                <div className="metatag">
                    {this.props.metatags}
                </div>
                <div className="lang">
                    {this.props.lang && this.props.lang}
                </div>
                <img id="blogThumb" ref="thumbnail" src={this.props.thumbnail  ? this.props.thumbnail : 'https://media.istockphoto.com/photos/abstract-background-picture-id537447438?k=6&m=537447438&s=612x612&w=0&h=YF98lsh0OIxChOs4XmOFmuzLxSj8sieD-CiK-V8iGfU='} className="card-img img-fluid" alt="..." />
                <div className="card-img-overlay">
                    <h4 className="card-title">{this.props.title || "Title"}</h4>
                    <p className="mt-n2">{this.props.subtitle || "Subttitle"}</p>
                </div>
              <div className="card-footer">
                    <span className="float-right">
                        <i className="fas fa-eye fa-lg"></i>&nbsp;&nbsp;{this.props.views || 123}
                    </span>
                    by {this.props.author}
                </div>
            </div>
            )
    }
}