import React, { Component } from 'react'
import './Blogcard.scss';
import CardExpanded from './CardExpanded.jsx'
export default class BlogCard extends Component {
    constructor(props) {
        super(props);
        this.state = {
           showPopUp:false,
           props:[]
        }
        this.openPopUp= this.openPopUp.bind(this)
    }
    openPopUp(value,gprops){
        this.setState({
            showPopUp:true,
            props:gprops
        })
        this.props.showExpanded(gprops)
    }
    componentDidMount(){
        console.log('props',this.props)
    }

    render() {
        if (this.props.type === 1) // regular blog style
            return <div className="blogCard all card text-left">
                <div className="row">
                    <div className="col-6 left">
                        <img src={this.props.thumbnail} className="thumbnail" alt="..." />
                        <div className="orange-bar"></div>
                        <ul>
                            {this.props.tags }
                        </ul>
                        
                    </div>
                    <div className="col-6">
                        <div className="metatag">
                            {this.props.metatags}
                        </div>
                        <div className="lang">{this.props.lang}</div>
                        <div className="card-body">
                            <h3 className="card-title">{this.props.title || "Title"}</h3>
                            <p className="mt-n2 card-subtitle">{this.props.subtitle || "Subttitle"}</p>
                            <p className="card-text mt-2">
                                {this.props.cont && this.props.cont.substring(0, 130)}{this.props.cont && this.props.cont.length > 130 ? '...' : ''}
                            </p>
                        </div>
                        <div className="card-footer">
                            {this.props.authorImg ? <img src={this.props.authorImg} className="authorImg" /> : ''}
                            by <span>{this.props.author}</span>
                        </div>

                    </div>
                </div>
            </div>

        else if (this.props.type === 2)// featured
            return <div className="blogCard featured card text-left " onClick={()=>this.openPopUp(true,this.props)}>
                <div className="metatag">
                    {this.props.metatags}
                </div>
                <div className="lang">
                    {this.props.lang && this.props.lang}
                </div>
                <img id="blogThumb" ref="thumbnail" src={this.props.thumbnail ? this.props.thumbnail : 'https://media.istockphoto.com/photos/abstract-background-picture-id537447438?k=6&m=537447438&s=612x612&w=0&h=YF98lsh0OIxChOs4XmOFmuzLxSj8sieD-CiK-V8iGfU='} className="card-img" alt="..." />
                <div className="card-img-overlay">
                    <h4 className="card-title">{this.props.title || "Title"}</h4>
                    <p className="mt-n2">{this.props.subtitle || "Subttitle"}</p>
                    <p className="card-text mt-n2">
                        {this.props.cont && this.props.cont.substring(0, 130)}{this.props.cont && this.props.cont.length > 130 ? '...' : ''}
                    </p>
                </div>
              <div className="card-footer">
                    <span className="float-right">
                        <i className="fas fa-eye fa-lg"></i>&nbsp;&nbsp;{this.props.views || 123}
                    </span>
                    by {this.props.author}
                </div>
            </div>
        else
            return <div className="blogCard latest card text-left " onClick={()=>this.openPopUp(true,this.props)}>
                <div className="lang">
                    {this.props.lang}
                </div>
                <div className="metatag orange-bar">
                    {this.props.metatags}
                </div>
                <img src={this.props.thumbnail} className="card-img-top" alt="..." />
                <div className="card-body">
                    <h4 className="card-title col-bl">{this.props.title || "Title"}</h4>
                    <p className="col-bl mt-n2">{this.props.subtitle || "Subttitle"}</p>
                    <p className="card-text mt-n2">
                        {this.props.cont && this.props.cont.substring(0, 130)}{this.props.cont && this.props.cont.length > 130 ? '...' : ''}
                    </p>
                </div>
                <div className="card-footer">
                    <span className="float-right">
                        <i className="fas fa-eye fa-lg"></i>&nbsp;&nbsp;{this.props.views || 123}
                    </span>
                    by {this.props.author}
                </div>
            </div>

                            
    }
}