import React from 'react'
import logo from './images/favicon.ico'
export default class header extends React.Component{
	constructor(props){
    super(props)
    this.state={
      show:false
    }
    this.btnClick = this.btnClick.bind(this)
  }
  btnClick(){
    this.setState({
      show:!this.state.show
    })
  }
  render(){
		return(
				<div>
<nav className="navbar navbar-expand-lg navbar-dark fixed-top mb-2">
  <a className="navbar-brand" href="#"><img className="img-fluid logo mt-2" src={logo} alt="image"/></a>
    <span className="nav-item text-right">
        <span className="mr-3 d-inline">Prelims Current Affair's</span>
        <span className="mr-3 d-inline">Motivation</span>
        <span className="mr-3 d-inline">Study Material</span>
        <span className=" d-inline">Contact Us</span>
      </span>
    <button className="btn-nav justify-content-center mt-0" onClick={this.btnClick}>
      <ul >
      <li className="ml-0 span1"></li>
      <li className="span1 ml-0 "></li>
      </ul>
    </button>
</nav>
        {this.state.show ?
        <div className="nav-dialog mt-5">
            <br/><br/><br/>
            <ul className="pt-3">
              <li>Prelims Current Affair's</li>
              <hr/>
              <li>Motivation</li>
              <hr/>
              <li>Study Material</li>
              <hr/>
              <li>Contact Us</li>
              <hr/>
            </ul>
        </div>
        :
        <span></span>}
				</div>
			)
	}
}