import React from 'react'

export default class CardExpanded extends React.Component{
	constructor(props){
		super(props)
		this.close = this.close.bind(this)
		this.createMarkup=this.createMarkup.bind(this)
		
	}
	close(){
		this.props.close(true)
	}
	createMarkup(){
		 return { __html: this.props.value.cont}
	}
	render(){
		return(
			<div className="card-expanded">
			<div className="img">
				<div className="title">
					<button className="btn btn-primary close-btn close" onClick={this.close}>&times;</button>
					<img className="img-tag img-fluid" alt="article-Image" src={this.props.value.thumbnail  ? this.props.value.thumbnail : 'https://media.istockphoto.com/photos/abstract-background-picture-id537447438?k=6&m=537447438&s=612x612&w=0&h=YF98lsh0OIxChOs4XmOFmuzLxSj8sieD-CiK-V8iGfU='}/>
				</div>
				<div className="text_card conatiner">
				<h2 className="title-overlay">{this.props.value.title}</h2>
					<p className="subTitle">{this.props.value.subtitle}</p>
					<p className="text-left">By: {this.props.value.author}</p>
					<div className="row">
						<div className="col-md-10 col-sm-10 col-lg-10">
							<p className="text-left">Tags: {this.props.value.tags}</p>
						</div>
					</div>
					<div className="content" dangerouslySetInnerHTML={this.createMarkup()} ></div>
				</div>
			</div>
			</div>
			)
	}

}