import React, { Component } from 'react';
import ScrollMenu from 'react-horizontal-scrolling-menu';


const ArrowLeft = <div className="arrow-prev"> <button><i className="fas fa-arrow-left fa-lg"></i></button> </div>;
const ArrowRight = <div className="arrow-next"> <button><i className="fas fa-arrow-right fa-lg"></i></button> </div>;

const ArrowLeftBlue = <div className="arrow-prev blue"> <button><i className="fas fa-arrow-left fa-lg"></i></button> </div>;
const ArrowRightBlue = <div className="arrow-next blue"> <button><i className="fas fa-arrow-right fa-lg"></i></button> </div>;

export default class Scroll extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return <ScrollMenu
            data={this.props.data}
            arrowLeft={this.props.style && this.props.style == 2 ? ArrowLeftBlue : ArrowLeft}
            arrowRight={this.props.style && this.props.style == 2 ? ArrowRightBlue : ArrowRight}
            scrollBy={1}
            alignCenter={false}
            wheel={true}
            hideSingleArrow={true}
        />
    }
};